@use './../../styles/mixins.scss';

.greeting {
  background: #d6efb0;
  height: 100vh;
  overflow: hidden;
  @include mixins.module_padding;
}

.greeting_wrap {
  @include mixins.container;
  justify-content: space-around;
  align-items: center;
}

.greeting_photo img {
  display: block;
  width: 100%;
}